import React from "react";

function Hero() {
  return (
    <section className="hero">
      <img
        className="collage"
        src={require("./images/collage.png")}
        alt="collage"
      />
      <div className="content">
        <h1>Online Experience</h1>
        <p>
          Every day, millions of Hosts on Airbnb invite the world to live, work,
          and play from anywhere. DISCOVER MILLIONS OF PLACES TO STAY
        </p>
      </div>
    </section>
  );
}

export default Hero;
